\r\n        <div class=\"close\" @click=\"closeWeather\"></div>\r\n      </div>\r\n      <div class=\"setup\" @click=\"showSetBox\"></div>\r\n    </div>\r\n    <div class=\"grid\" v-if=\"list\">\r\n      <ul>\r\n        <li v-if=\"list\" v-for=\"item in list\" track-by=\"_uid\">\r\n          <div class=\"item\">\r\n            <a :href=\"item.tar\" target=\"blank\">\r\n              <div class=\"imgr\" :data-label=\"item.title\" v-bind:style=\"{backgroundImage: 'url('+item.url+')'}\"></div>\r\n            </a>\r\n            <div class=\"ifo row alignItem\">\r\n              <div class=\"date\">{{item.time | formatDate}}</div>\r\n              <div class=\"cell\"><span class=\"like\" :data-uid=\"item._uid\" @click=\"vote\">{{item.like}}</span></div>\r\n              <div class=\"del\" @click=\"delate(item._uid)\"></div>\r\n            </div>\r\n          </div>\r\n        </li>\r\n      </ul>\r\n    </div>\r\n    <div class=\"addBtn\" @click=\"showEditBox\"></div>\r\n    <div class=\"loginBox\" :class=\"{'show': showEdit}\">\r\n        <div class=\"box\">\r\n          <div v-bind:style=\"{animation: shakeAnimate}\">\r\n            <input type=\"text\" placeholder=\"\" id=\"pTitle\">\r\n            <input type=\"text\" placeholder=\"URL\" id=\"pUrl\">\r\n            <input type=\"text\" placeholder=\"\" id=\"pTar\">\r\n            <input type=\"button\" value=\"\" id=\"submit\" @click=\"pushData\">\r\n            <i class=\"close\" @click=\"hideEditBox\"></i>\r\n          </div>\r\n        </div>\r\n    </div>\r\n    <div class=\"loginBox\" :class=\"{'show': setup}\">\r\n        <div class=\"box\">\r\n          <div>\r\n            <input type=\"text\" placeholder=\"\" id=\"sName\">\r\n            <input type=\"text\" placeholder=\"1208357RUL\" id=\"sUrl\">\r\n            <input type=\"text\" placeholder=\"260260URL\" id=\"sAva\">\r\n            <input type=\"button\" value=\"\" id=\"submit\" @click=\"modifyIfo\">\r\n            <i class=\"close\" @click=\"hideSetBox\"></i>\r\n          </div>\r\n        </div>\r\n    </div>  \r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport Vue from 'Vue'\r\nimport Firebase from 'firebase'\r\nconst api = new Firebase('https://zenway.firebaseio.com/')\r\nrequire('../../libs/bubble')\r\n\r\n//\r\nfunction formatDate(t){\r\n  if(!t){\r\n    t = (new Date()).getTime();\r\n  }\r\n  var time = new Date(t*1);\r\n  var y = time.getFullYear();\r\n  var m = time.getMonth()+1;\r\n  m<9 ? (m = '0' + m) : null;\r\n  var d = time.getDate();\r\n  d<9 ? (d = '0' + d) : null;\r\n  var h = time.getHours();\r\n  h<9 ? (h = '0' + h) : null;\r\n  var n = time.getMinutes();\r\n  n<9 ? (n = '0' + n) : null;\r\n  var str = y +'-'+ m +'-'+ d +' '+ h +':'+ n;\r\n  return str;\r\n}\r\n\r\nVue.filter('formatDate', formatDate)\r\n\r\nexport default {\r\n  name: 'home',\r\n  props: {\r\n    user: ''\r\n  },\r\n  data () {\r\n    return {\r\n      log: 'Hello home!',\r\n      homeData: null,\r\n      list: null,\r\n      showEdit: false,\r\n      setup: false,\r\n      shakeAnimate: '',\r\n      weather: '',\r\n      weatherState: false,\r\n      changeState: false\r\n    }\r\n  },\r\n  events: {\r\n    setUser: function(params){\r\n      this.user = params;\r\n      //\r\n      this.initPage(this.user);\r\n    }\r\n  },\r\n  beforeDestroy () {\r\n    this.bubble.stop();\r\n    this.bubble = null;\r\n  },\r\n  destroyed () {\r\n    window.scrollTo(0, 0);\r\n  },\r\n  ready () {\r\n    //\r\n    this.initPage(this.user);\r\n    //\r\n    this.bubble = new bubble('bubble',1208, 356);\r\n    //\r\n    this.localGetWeather();\r\n  \r\n  },\r\n  methods: {\r\n    localGetWeather: function(){\r\n      var view = this;\r\n      var city = '';\r\n      if(remote_ip_info.country == \"\") city = remote_ip_info.city;\r\n      view.getWeather(city, function(data){\r\n        //console.log(data);\r\n        view.weather = data;\r\n      });\r\n    },\r\n    showChange: function(event){\r\n      var e = event || window.event;  \r\n      if(e.stopPropagation){   \r\n          e.stopPropagation();  \r\n      }else{  \r\n          e.cancelBubble = true; \r\n      }  \r\n      this.changeState = true;\r\n    },\r\n    hideChange: function(){\r\n      this.changeState = false;\r\n    },\r\n    change: function(){\r\n      var view = this;\r\n      this.changeState = false;\r\n      var newLocal = this.$els.newLocal.value;\r\n      if(newLocal){\r\n        this.getWeather(newLocal, function(data){\r\n          if(data){\r\n            view.weather = data;\r\n          }else{\r\n            view.$dispatch('error', '');\r\n          }\r\n        });\r\n      }\r\n    },\r\n    getWeather: function(city, callback){\r\n      var view = this;\r\n      $.ajax({\r\n        type: \"GET\",\r\n        url: \"http://wthrcdn.etouch.cn/weather_mini?city=\"+ city,\r\n        dataType: \"jsonp\",\r\n        success: function(data){\r\n          if(callback) callback(data.data);\r\n        },\r\n        complete: function(){\r\n          //console.log('');\r\n        },\r\n        error: function(){  \r\n          view.$dispatch('error', '');\r\n        }  \r\n      });\r\n    },\r\n    toggleWeather: function(){\r\n      this.weatherState ? this.weatherState = false : this.weatherState = true;\r\n    },\r\n    closeWeather: function(){\r\n      this.weatherState = false;\r\n    },\r\n    shakeBox: function(){\r\n      var tmpTimer;\r\n      var view = this;\r\n      tmpTimer = setTimeout(function(){\r\n        view.shakeAnimate = 'shake .3s';\r\n        clearTimeout(tmpTimer);\r\n      },200);\r\n    },\r\n    clearShake: function(){\r\n      this.shakeAnimate = '';\r\n    },\r\n    initPage: function(uid){\r\n      var view = this;\r\n      view.getUserData(uid, function(data){\r\n        view.homeData = data;\r\n        view.shunt(view.homeData.list);\r\n      })\r\n    },\r\n    resetData: function(uid){\r\n      var view = this;\r\n      view.getUserData(uid, function(data){\r\n        view.homeData = data;\r\n      })\r\n    },\r\n    shunt: function(DATA){\r\n      var uid = [];\r\n      var listItems = [];\r\n      var x;\r\n      for(x in DATA){\r\n        uid.push(x);\r\n        var tmp = DATA[x];\r\n        tmp._uid = x;\r\n        listItems.push(tmp);\r\n      }\r\n      var view = this;\r\n      listItems.reverse();\r\n      var dataLength = listItems.length;\r\n      var end = 8;\r\n      if(dataLength < 8){\r\n        end = dataLength;\r\n      }\r\n      view.list = listItems.slice(0, end);\r\n      var clientHeight = document.body.clientHeight;\r\n      $(window).resize(function(){clientHeight = document.body.clientHeight;})\r\n      $(window).scroll(function(e){\r\n        var pageHeight = document.body.scrollHeight;\r\n        var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;\r\n        var distance = pageHeight - scrollTop - clientHeight;      \r\n        if(distance < 400 && end < dataLength){\r\n            if(dataLength - end < 4){\r\n              end+=(dataLength - end);\r\n            }\r\n            end+=4;\r\n            view.list = listItems.slice(0, end);\r\n        }\r\n      })\r\n    },\r\n    vote: function(event){\r\n      var view = this;\r\n      var tar = $(event.target);\r\n      var uid = tar.data('uid');\r\n      var count = tar.text()*1;\r\n      if(!tar.hasClass('active')){\r\n        tar.addClass('active');\r\n        count+=1;\r\n      }else{\r\n        tar.removeClass('active');\r\n        count-=1;\r\n      }\r\n      tar.text(count);\r\n      view.updateData(view.user+'/home/list/'+uid, {like: count});\r\n    },\r\n    delate: function(uid){\r\n      var view = this;\r\n      window.confirms('', '', function(){\r\n        var onComplete = function(error) {\r\n          if (error) {\r\n            view.$dispatch('error', '');\r\n          } else {\r\n            view.$dispatch('toast', '');\r\n            //\r\n            view.initPage(view.user);\r\n          }\r\n        };\r\n        api.child(view.user+'/home/list/'+uid).remove(onComplete);\r\n      })\r\n    },\r\n    updateData: function(uid, params){\r\n      var view = this;\r\n      api.child(uid).update(params)\r\n    },\r\n    showEditBox: function(){\r\n      var $body = $('body');\r\n      $body.animate({scrollTop:0}, 300, function(){\r\n        $body.addClass('dialog');\r\n      });\r\n      this.showEdit = true;\r\n    },\r\n    showSetBox: function(){\r\n      var $body = $('body');\r\n      $body.animate({scrollTop:0}, 300, function(){\r\n        $body.addClass('dialog');\r\n      });\r\n      this.setup = true;\r\n    },\r\n    hideEditBox: function(){\r\n      this.showEdit = false;\r\n      $('body').removeClass('dialog');\r\n    },\r\n    hideSetBox: function(){\r\n      this.setup = false;\r\n      $('body').removeClass('dialog');\r\n    },\r\n    pushData: function(){\r\n      var view = this;\r\n      view.clearShake();\r\n      var title = $('#pTitle').val();\r\n      var urls = $('#pUrl').val();\r\n      var target = $('#pTar').val();\r\n      if(title && urls && target){\r\n        var params = {\r\n          \"time\": (new Date()).getTime(),\r\n          \"like\": 0,\r\n          \"title\": title,\r\n          \"url\": urls,\r\n          \"tar\": target\r\n        };\r\n        api.child(view.user+'/home/list/').push(params, function(err){\r\n          if(err){\r\n            view.$dispatch('error', '');\r\n          }else{\r\n            view.$dispatch('toast', '');\r\n            //\r\n            view.initPage(view.user);\r\n          }\r\n        });\r\n        $('#pTitle').val('');\r\n        $('#pUrl').val('');\r\n        $('#pTar').val('');\r\n        this.hideEditBox();\r\n        \r\n      }else{\r\n        view.shakeBox(); \r\n      }\r\n    },\r\n    modifyIfo: function(){\r\n      var view = this;\r\n      var sName = $('#sName').val();\r\n      var sUrl = $('#sUrl').val();\r\n      var sAva = $('#sAva').val();\r\n      if(sName || sUrl || sAva){\r\n        sName && api.child(view.user+'/home/userName/').set(sName);\r\n        sUrl && api.child(view.user+'/home/theme/').set(sUrl);\r\n        sAva && api.child(view.user+'/home/avatar/').set(sAva);\r\n        //\r\n        view.resetData(view.user);\r\n      }\r\n      //\r\n      $('#sName').val('');\r\n      $('#sUrl').val('');\r\n      $('#sAva').val('');\r\n      this.hideSetBox();\r\n    },\r\n    //\r\n    getUserData: function(uid, callback, errorCallback){\r\n      var view = this;\r\n      view.$dispatch('waitPanel', true);\r\n      api.child(uid+'/home').once(\"value\", function(snapshot){\r\n        var data = snapshot.val();\r\n        callback(data);\r\n        view.$dispatch('waitPanel', false);\r\n      }, function(err){\r\n          if(errorCallback) errorCallback();\r\n          view.$dispatch('waitPanel', false);\r\n          var errStr = \": \" + err.code;\r\n          view.$dispatch('error', errStr);\r\n      });\r\n    }\r\n  }\r\n}\r\n</script>"],"sourceRoot":"webpack://"}]);

