<template>
  <header>
    <div class="header" @mouseout="hideCodeBox">
      <div class="logo"><img src="../assets/cube.png" height="50"></div>
      <div class="center">
        <ul class="guide">
          <li v-link="{ name: 'home', activeClass: 'active' }"></li>
          <li v-link="{ name: 'note', activeClass: 'active' }"></li>
          <li v-link="{ name: 'games', activeClass: 'active' }"></li>
          <li></li>
          <li></li>
        </ul>
        <div class="content">
          <ul>
            <li>
              <a @mouseover="showCodeBox | debounce 300" class="wechat"></a>
              <div class="qrcode" :class="{'show': showCode}"><img src="../assets/qrcode.png"></div>
            </li>
            <li><a href="http://user.qzone.qq.com/741849580/main" target="blank" title="QQ" class="qzone"></a></li>
            <li><a href="http://weibo.com/zyh0921/" target="blank" title="" class="sina"></a></li>
            <li><a title="" class="reg" @click="showRegBox"></a></li>
          </ul>
        </div>
      </div>
      <div class="sign">
        <a href="javascript:;" v-if="hasUser" @click="logout"></a>
        <a href="javascript:;" v-else @click="showLoginBox"></a>
      </div>
    </div>
    <div class="loginBox" :class="{'show': showLogin}">
        <div class="box">
          <div v-bind:style="{animation: shakeAnimate}">
            <input type="text" placeholder="" id="userMail">
            <input type="password" placeholder="" id="userPassword">
            <input type="button" value="" id="submit" @click="login">
            <i class="close" @click="closeLoginBox"></i>
          </div>
        </div>
    </div>
    <div class="loginBox regBox" :class="{'show': showReg}">
        <div class="box">
          <div v-bind:style="{animation: shakeAnimate}">
            <input type="text" placeholder="" id="regMail">
            <input type="password" placeholder="" id="regPassword">
            <input type="password" placeholder="" id="regPasswordAgain">
            <input type="button" value="" id="submit" @click="reg">
            <i class="close" @click="closeRegBox"></i>
          </div>
        </div>
    </div>  
  </header>
</template>

<script>
import dataTemplate from '../libs/dataTemplate'
const api = new Firebase('https://zenway.firebaseio.com/')

export default {
  name: 'AppHeader',
  data () {
    return {
      title: 'Hello header!',
      showLogin: false,
      showReg: false,
      hasUser: false,
      showCode: false,
      shakeAnimate: ''
    }
  },
  ready () {
    var view = this;
    var defaultUID = view.GetCookie('musedefaultusercounter');
    if(defaultUID){
      view.hasUser = true;
      view.$dispatch('userLogin', defaultUID);
    }
  },
  methods: {
    showLoginBox: function(){
      this.showLogin = true;
      var $body = $('body');
      $body.animate({scrollTop:0}, 300, function(){
        $body.addClass('dialog');
      });
    },
    shakeBox: function(){
      var tmpTimer;
      var view = this;
      tmpTimer = setTimeout(function(){
        view.shakeAnimate = 'shake .3s';
        clearTimeout(tmpTimer);
      },200);
    },
    clearShake: function(){
      this.shakeAnimate = '';
    },
    GetCookie: function(name){
      var arg = name + "=";
      var alen = arg.length;
      var clen = window.document.cookie.length;
      var i = 0;
      while (i < clen) {
          var j = i + alen;
          if (window.document.cookie.substring(i, j) == arg) return this.getCookieVal(j);
          i = window.document.cookie.indexOf(" ", i) + 1;
          if (i == 0) break;
      }
      return null;
    },
    getCookieVal: function(offset){
      var endstr = window.document.cookie.indexOf(";", offset);
      if (endstr == -1) endstr = window.document.cookie.length;
      return unescape(window.document.cookie.substring(offset, endstr));
    },
    SetCookie: function(name, value){
      var exp = new Date();
      exp.setTime(exp.getTime() + (2 * 24 * 60 * 60 * 1000));
      window.document.cookie = name + "=" + escape(value) + "; expires=" + exp.toGMTString() + ";path=/";
    },
    DeleteCookie: function(name) {
      var exp = new Date();
      exp.setTime(exp.getTime() - 100);
      var cval = this.GetCookie(name);
      window.document.cookie = name + "=" + cval + "; expires=" + exp.toGMTString() + ";path=/";
    },
    login: function(){
      var view = this;
      view.clearShake();
      var mail = $('#userMail').val();
      var pswd = $('#userPassword').val();
      if(mail && pswd){
        view.closeLoginBox();
        view.$dispatch('waitPanel', true);
        api.authWithPassword({
          email : mail,
          password : pswd
        }, function(error, authData) {
          if (error) {
            //console.log("Login Failed!", error);
            view.$dispatch('waitPanel', false);
            view.$dispatch('error', '');
          } else {
            //console.log("Authenticated successfully with payload:", authData);
            view.hasUser = true;
            view.$dispatch('waitPanel', false);
            view.$dispatch('toast', '');
            view.$dispatch('userLogin', authData.uid);
            //cookie()
            view.SetCookie('musedefaultusercounter', authData.uid);
          }
        });
      }else{
         view.shakeBox(); 
         view.$dispatch('error', '');
      }
    },
    closeLoginBox: function(){
      this.showLogin = false;
      $('body').removeClass('dialog');
    },
    showCodeBox: function(){
      this.showCode = true;
    },
    hideCodeBox: function(){
      this.showCode = false;
    },
    showRegBox: function(){
      this.showReg = true;
    },
    closeRegBox: function(){
      this.showReg = false;
    },
    logout: function(){
      var view = this;
      view.hasUser = false;
      view.user = "user-000000";
      view.$dispatch('toast', '');
      view.$dispatch('userLogin', view.user);
      view.DeleteCookie('musedefaultusercounter');
    },
    reg: function(){
      var view = this;
      view.clearShake();
      console.log();
      var mail = $('#regMail').val();
      var pswd = $('#regPassword').val();
      var pswdAgn = $('#regPasswordAgain').val();
      var regMail = /^[\w-]+(\.[\w-]+)*@[\w-]+(\.[\w-]+)+$/;
      var regPswd = /^[a-zA-Z_]\w{5,19}$/; //6-20
      if(regMail.test(mail) && regPswd.test(pswd) && pswd == pswdAgn){
        view.closeRegBox();
        view.$dispatch('waitPanel', true);
        api.createUser({
          email : mail,
          password : pswd
        }, function(error, userData) {
          if (error) {
            //console.log("Error creating user:", error);
            view.$dispatch('waitPanel', false);
            view.$dispatch('error', '');
          } else {
            //console.log("Successfully created user account with uid:", userData.uid);
            view.$dispatch('waitPanel', false);
            view.$dispatch('toast', '');
            var newUid = userData.uid;
            //cookie()
            view.hasUser = true;
            view.SetCookie('musedefaultusercounter', newUid);
            //
            var DEFAULT = {};
            DEFAULT[newUid] = dataTemplate;
            api.update(DEFAULT);
            view.$dispatch('userLogin', newUid);
          }
        });
      }else{
        view.$dispatch('error', '');  
        view.shakeBox();
        if(!regMail.test(mail)){
          view.$dispatch('error', '');
        }else if(!regPswd.test(pswd)){
          view.$dispatch('error', '6-20');
        }else if(pswd != pswdAgn){
          view.$dispatch('error', '');
        }
      }
    }
  }
}
</script>


